:root {
  --menu--transition: transform 0.195s cubic-bezier(0.694, 0.0482, 0.335, 1);
  --brand-color: #002347; }

label,
.form-label {
  color: var(--brand-color);
  font-size: 1rem;
  font-weight: bold;
  line-height: 1rem; }

select {
  color: #212121; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-weight: normal; }

input::placeholder,
textarea::placeholder {
  opacity: 0.5;
  color: inherit; }

[role='button'],
button {
  cursor: pointer; }

input,
.form-input {
  height: 2.25rem;
  margin-bottom: 0;
  color: #212121;
  border: 1px solid var(--brand-color);
  border-radius: 2px;
  font-size: 16px;
  line-height: 2.25rem; }
  input:focus,
  .form-input:focus {
    transition: border linear .2s, box-shadow linear .2s;
    border: 1px solid var(--brand-color);
    outline-style: none;
    box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.4), 0 0 8px rgba(0, 35, 71, 0.5); }

textarea,
.form-textarea {
  padding: .5rem;
  resize: vertical;
  color: #212121;
  border: 1px solid var(--brand-color);
  border-radius: 2px;
  font-size: 16px;
  line-height: 2.25rem; }
  textarea:focus,
  .form-textarea:focus {
    transition: border linear .2s, box-shadow linear .2s;
    border: 1px solid var(--brand-color);
    outline-style: none;
    box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.4), 0 0 8px rgba(0, 35, 71, 0.5); }

.switch [type='checkbox'] {
  visibility: hidden;
  width: 0;
  height: 0; }

.switch label {
  display: block;
  position: relative;
  width: 3rem;
  height: 1.5rem;
  cursor: pointer;
  text-indent: -9999px;
  border-radius: 1rem;
  background: #ccc; }

.switch label::after {
  position: absolute;
  top: 1px;
  left: 1px;
  width: 1.4rem;
  height: 1.4rem;
  content: '';
  transition: .125s;
  border-radius: 1.4rem;
  background: #fff; }

.switch input:checked + label {
  background: var(--brand-color); }

.switch input:checked + label::after {
  left: calc(100% - 1px);
  transform: translateX(-100%); }

.styled-select {
  display: inline-block;
  overflow: hidden;
  width: 100%;
  max-width: 100%;
  margin-bottom: 1.5rem; }

.styled-select select {
  width: 100%;
  height: 2.625rem;
  padding-right: 3rem;
  padding-left: .5rem;
  transition: color 0.125s ease, background-color 0.125s ease;
  border: 1px solid var(--brand-color);
  border-radius: 2px;
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: center;
  background-size: 42px 42px;
  line-height: 1;
  -webkit-appearance: initial;
  cursor: pointer; }

.styled-text-input label,
.styled-textarea label {
  display: block; }

.styled-radios,
.styled-checkboxes {
  margin-top: 1.5rem; }

.form--inline {
  display: flex; }

.form--inline .styled-radios,
.form--inline .styled-checkboxes {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0; }
  .form--inline .styled-radios .checkbox,
  .form--inline .styled-radios .radio,
  .form--inline .styled-checkboxes .checkbox,
  .form--inline .styled-checkboxes .radio {
    padding-right: 1.25rem; }

.checkbox,
.radio {
  position: relative; }

.checkbox input,
.radio input {
  position: absolute;
  left: 0;
  width: 0;
  height: 0;
  margin: 0;
  opacity: 0; }

.checkbox label,
.radio label {
  display: inline-block;
  position: relative;
  margin-bottom: 0;
  padding-left: 2rem;
  line-height: 1.25rem;
  cursor: pointer; }

.checkbox label::after,
.radio label::after,
.checkbox label::before,
.radio label::before {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 1.25rem;
  height: 1.25rem;
  content: ''; }

.checkbox label::before,
.radio label::before {
  border: solid 2px #70818a; }

.radio label::before {
  border-radius: 3.125rem; }

.checkbox input:disabled + label,
.radio input:disabled + label {
  cursor: not-allowed; }

.checkbox input:disabled + label::before,
.radio input:disabled + label::before {
  background-color: #dae4e9; }

.checkbox input + label::after,
.radio input + label::after {
  content: none; }

.checkbox input:checked + label::after,
.radio input:checked + label::after {
  content: ''; }

.radio input:checked + label::after {
  top: 0;
  left: 0;
  width: 1.25rem;
  height: 1.25rem;
  border: 3px solid #dae4e9;
  border-radius: 3.125rem;
  background-color: var(--brand-color); }

.checkbox input:checked + label::after {
  top: 5px;
  left: 5px;
  width: .714rem;
  height: .429rem;
  transform: rotate(-45deg);
  border-color: var(--brand-color);
  border-bottom: 3px solid;
  border-left: 3px solid; }

.radio input:checked:disabled + label::after {
  background-color: #dae4e9; }

.checkbox input:checked:disabled + label::after {
  border-color: #dae4e9; }

.form-required {
  color: #f00; }

.submit-button {
  border-color: var(--brand-color);
  padding: .5rem 1rem;
  line-height: 1;
  margin-bottom: 1.5rem;
  transition: color 0.125s ease, background-color 0.125s ease;
  cursor: pointer; }
  .submit-button:hover {
    background-color: #dae4e9; }
